@import "https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;600&display=swap";
* {
  box-sizing: border-box;
}

html {
  --bg: #efefef;
  --fg: #1e1e1e;
  --gray: #555;
  --light-gray: #ccc;
  --shadow: #e6e6e6;
  --success: #5ace84;
  --primary: #ff585d;
  --secondary: #0072ce;
  background-color: var(--bg);
  color: var(--fg);
  background-image: url("bc_coffee_shop_3.d4d7b1f6.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  font-size: calc(.9em + .5vw);
  line-height: 1.3;
}

body {
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.logo {
  text-align: center;
  font-size: 70px;
}

.footer_img {
  height: 15px;
}

.logo > img {
  height: 100px;
}

.bottom {
  background-color: #1e1e1e;
  min-width: 50%;
  margin: auto;
  font-size: 12px;
  display: block;
  bottom: 0;
}

footer {
  box-sizing: border-box;
  background-color: #1e1e1e;
  background-color: color(srgb .1176 .1176 .1177);
  width: 100%;
  padding-bottom: 10px;
  font-size: 12px;
  position: fixed;
  bottom: 0;
}

main {
  flex: 1 0 auto;
  max-width: 26em;
  margin: 0 auto;
  padding-bottom: 100px;
}

h1 {
  text-align: center;
  background-position: 50% 1em;
  background-repeat: no-repeat;
  background-size: auto 1.5em;
  margin-top: 0;
  padding: 3.5em 0 0;
  font-size: 1.5em;
}

.greeting {
  color: var(--secondary);
  text-decoration: underline;
}

h2 {
  text-align: center;
}

.please-wait .change {
  pointer-events: none;
}

ul.information {
  text-align: left;
  margin: 2em 0;
  padding: 0;
  font-size: .8em;
}

.information li:first-child {
  border-top: 1px solid var(--light-gray);
}

.information li {
  border-bottom: 1px solid var(--light-gray);
  padding: .5em 0;
  list-style: none;
}

.change {
  border: 2px solid var(--light-gray);
  background-color: #1e1e1e;
  flex-direction: column;
  place-content: stretch space-evenly;
  align-items: stretch;
  padding: .5em;
  font-size: 1em;
  display: flex;
}

.change > div {
  place-content: stretch space-evenly;
  align-items: stretch;
  display: flex;
}

.change input {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  flex: 1;
}

.change label {
  text-align: left;
  margin-right: 10px;
  padding-bottom: .5em;
  display: block;
}

.change button {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

a, .link {
  color: var(--primary);
  text-decoration: none;
}

a:hover, a:focus, .link:hover, .link:focus {
  text-decoration: underline;
}

a:active, .link:active {
  color: var(--secondary);
}

button, input {
  font: inherit;
  outline: none;
}

main.please-wait .change button {
  pointer-events: none;
  background-color: #fff;
  position: relative;
}

main.please-wait .change button span {
  visibility: hidden;
}

button {
  background-color: var(--secondary);
  color: #efefef;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: .3em .75em;
  transition: transform 30ms;
}

button:hover, button:focus {
  box-shadow: inset 0 0 10em #fff3;
}

input {
  background-color: var(--light-gray);
  caret-color: var(--primary);
  color: inherit;
  border: none;
  border-radius: 5px 0 0 5px;
  padding: .25em .5em;
}

input::selection {
  background-color: var(--secondary);
  color: #efefef;
}

input:focus {
  box-shadow: inset 0 0 10em #00000005;
}

code {
  color: var(--gray);
}

li {
  padding-bottom: 1em;
}

select {
  appearance: none;
  box-shadow: none;
  color: #fff;
  background-color: var(--darkgray);
  cursor: pointer;
  background-image: none;
  border: 0;
  outline: 10px red;
  flex: 1;
  padding: 0 1em;
}

select::-ms-expand {
  display: none;
}

.select {
  border: 1px solid #2f4f4f;
  border-radius: .25em;
  width: 35%;
  height: 3em;
  display: flex;
  position: relative;
  overflow: hidden;
}

.select:after {
  content: "â–¼";
  pointer-events: none;
  background-color: #34495e;
  padding: 1em;
  transition: all .25s;
  position: absolute;
  top: 0;
  right: 0;
}

.select:hover:after {
  color: #f39c12;
}

.button-49, .button-49:after {
  color: #fff;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background: linear-gradient(to top right, #0000 5%, #d19e73 5%);
  border: 0;
  outline: #0000;
  width: 150px;
  height: 76px;
  font-family: Bebas Neue, sans-serif;
  font-size: 20px;
  position: relative;
  box-shadow: 6px 0 #b8860b;
}

.button-49:after {
  --slice-0: inset(50% 50% 50% 50%);
  --slice-1: inset(80% -6px 0 0);
  --slice-2: inset(50% -6px 30% 0);
  --slice-3: inset(10% -6px 85% 0);
  --slice-4: inset(40% -6px 43% 0);
  --slice-5: inset(80% -6px 5% 0);
  content: "NEAR protocol";
  text-shadow: -3px -3px #f8f005, 3px 3px #00e6f6;
  clip-path: var(--slice-0);
  background: linear-gradient(to top right, #0000 3%, #1e1e1e 3% 7.98%, #1e1e1e 38.66%);
  display: block;
  position: absolute;
  inset: 0;
}

.button-49:hover:after {
  animation: 1s steps(2, end) glitch;
}

.button-50, .button-50:after {
  color: #fff;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background: linear-gradient(45deg, #0000 5%, #ff013c 5%);
  border: 0;
  outline: #0000;
  width: 150px;
  height: 76px;
  font-family: Bebas Neue, sans-serif;
  font-size: 20px;
  position: relative;
  box-shadow: 6px 0 #b9c3c4;
}

.button-50:after {
  --slice-0: inset(50% 50% 50% 50%);
  --slice-1: inset(80% -6px 0 0);
  --slice-2: inset(50% -6px 30% 0);
  --slice-3: inset(10% -6px 85% 0);
  --slice-4: inset(40% -6px 43% 0);
  --slice-5: inset(80% -6px 5% 0);
  content: "EXIT";
  text-shadow: -3px -3px #f8f005, 3px 3px #00e6f6;
  clip-path: var(--slice-0);
  background: linear-gradient(45deg, #0000 3%, #00e6f6 3% 5%, #ff013c 5%);
  display: block;
  position: absolute;
  inset: 0;
}

.button-50:hover:after {
  animation: 1s steps(2, end) glitch;
}

.intro {
  background-color: #1e1e1e99;
  padding: 5px;
}

section {
  padding: 20px;
}

.hero {
  color: #fff;
  z-index: 2;
  letter-spacing: 10px;
  filter: drop-shadow(0 1px 3px);
  font-size: clamp(20px, 5vw, 50px);
  line-height: 1;
  display: inline-block;
}

.demo {
  background: #fff;
  height: 100px;
}

.layers {
  position: relative;
}

.layers:before, .layers:after {
  content: attr(data-text);
  z-index: -1;
  width: 110%;
  position: absolute;
}

.layers:before {
  color: #e0287d;
  top: 10px;
  left: 15px;
}

.layers:after {
  color: #1bc7fb;
  top: 5px;
  left: -10px;
}

.single-path {
  clip-path: polygon(0% 12%, 53% 12%, 53% 26%, 25% 26%, 25% 86%, 31% 86%, 31% 0%, 53% 0%, 53% 84%, 92% 84%, 92% 82%, 70% 82%, 70% 29%, 78% 29%, 78% 65%, 69% 65%, 69% 66%, 77% 66%, 77% 45%, 85% 45%, 85% 26%, 97% 26%, 97% 28%, 84% 28%, 84% 34%, 54% 34%, 54% 89%, 30% 89%, 30% 58%, 83% 58%, 83% 5%, 68% 5%, 68% 36%, 62% 36%, 62% 1%, 12% 1%, 12% 34%, 60% 34%, 60% 57%, 98% 57%, 98% 83%, 1% 83%, 1% 53%, 91% 53%, 91% 84%, 8% 84%, 8% 83%, 4% 83%);
}

.paths {
  animation: 5s step-end infinite paths;
}

@keyframes paths {
  0% {
    clip-path: polygon(0% 43%, 83% 43%, 83% 22%, 23% 22%, 23% 24%, 91% 24%, 91% 26%, 18% 26%, 18% 83%, 29% 83%, 29% 17%, 41% 17%, 41% 39%, 18% 39%, 18% 82%, 54% 82%, 54% 88%, 19% 88%, 19% 4%, 39% 4%, 39% 14%, 76% 14%, 76% 52%, 23% 52%, 23% 35%, 19% 35%, 19% 8%, 36% 8%, 36% 31%, 73% 31%, 73% 16%, 1% 16%, 1% 56%, 50% 56%, 50% 8%);
  }

  5% {
    clip-path: polygon(0% 29%, 44% 29%, 44% 83%, 94% 83%, 94% 56%, 11% 56%, 11% 64%, 94% 64%, 94% 70%, 88% 70%, 88% 32%, 18% 32%, 18% 96%, 10% 96%, 10% 62%, 9% 62%, 9% 84%, 68% 84%, 68% 50%, 52% 50%, 52% 55%, 35% 55%, 35% 87%, 25% 87%, 25% 39%, 15% 39%, 15% 88%, 52% 88%);
  }

  30% {
    clip-path: polygon(0% 53%, 93% 53%, 93% 62%, 68% 62%, 68% 37%, 97% 37%, 97% 89%, 13% 89%, 13% 45%, 51% 45%, 51% 88%, 17% 88%, 17% 54%, 81% 54%, 81% 75%, 79% 75%, 79% 76%, 38% 76%, 38% 28%, 61% 28%, 61% 12%, 55% 12%, 55% 62%, 68% 62%, 68% 51%, 0% 51%, 0% 92%, 63% 92%, 63% 4%, 65% 4%);
  }

  45% {
    clip-path: polygon(0% 33%, 2% 33%, 2% 69%, 58% 69%, 58% 94%, 55% 94%, 55% 25%, 33% 25%, 33% 85%, 16% 85%, 16% 19%, 5% 19%, 5% 20%, 79% 20%, 79% 96%, 93% 96%, 93% 50%, 5% 50%, 5% 74%, 55% 74%, 55% 57%, 96% 57%, 96% 59%, 87% 59%, 87% 65%, 82% 65%, 82% 39%, 63% 39%, 63% 92%, 4% 92%, 4% 36%, 24% 36%, 24% 70%, 1% 70%, 1% 43%, 15% 43%, 15% 28%, 23% 28%, 23% 71%, 90% 71%, 90% 86%, 97% 86%, 97% 1%, 60% 1%, 60% 67%, 71% 67%, 71% 91%, 17% 91%, 17% 14%, 39% 14%, 39% 30%, 58% 30%, 58% 11%, 52% 11%, 52% 83%, 68% 83%);
  }

  76% {
    clip-path: polygon(0% 26%, 15% 26%, 15% 73%, 72% 73%, 72% 70%, 77% 70%, 77% 75%, 8% 75%, 8% 42%, 4% 42%, 4% 61%, 17% 61%, 17% 12%, 26% 12%, 26% 63%, 73% 63%, 73% 43%, 90% 43%, 90% 67%, 50% 67%, 50% 41%, 42% 41%, 42% 46%, 50% 46%, 50% 84%, 96% 84%, 96% 78%, 49% 78%, 49% 25%, 63% 25%, 63% 14%);
  }

  90% {
    clip-path: polygon(0% 41%, 13% 41%, 13% 6%, 87% 6%, 87% 93%, 10% 93%, 10% 13%, 89% 13%, 89% 6%, 3% 6%, 3% 8%, 16% 8%, 16% 79%, 0% 79%, 0% 99%, 92% 99%, 92% 90%, 5% 90%, 5% 60%, 0% 60%, 0% 48%, 89% 48%, 89% 13%, 80% 13%, 80% 43%, 95% 43%, 95% 19%, 80% 19%, 80% 85%, 38% 85%, 38% 62%);
  }

  1%, 7%, 33%, 47%, 78%, 93% {
    clip-path: none;
  }
}

.movement {
  animation: 8s step-end infinite movement;
  position: relative;
}

@keyframes movement {
  0% {
    top: 0;
    left: -20px;
  }

  15% {
    top: 10px;
    left: 10px;
  }

  60% {
    top: 5px;
    left: -10px;
  }

  75% {
    top: -5px;
    left: 20px;
  }

  100% {
    top: 10px;
    left: 5px;
  }
}

.opacity {
  animation: 5s step-end infinite opacity;
}

@keyframes opacity {
  0% {
    opacity: .1;
  }

  5% {
    opacity: .7;
  }

  30% {
    opacity: .4;
  }

  45% {
    opacity: .6;
  }

  76% {
    opacity: .4;
  }

  90% {
    opacity: .8;
  }

  1%, 7%, 33%, 47%, 78%, 93% {
    opacity: 0;
  }
}

.font {
  animation: 7s step-end infinite font;
}

@keyframes font {
  0% {
    color: #e0287d;
    filter: blur(3px);
    font-weight: 100;
  }

  20% {
    color: #fff;
    filter: blur();
    font-weight: 500;
  }

  50% {
    color: #1bc7fb;
    filter: blur(2px);
    font-weight: 300;
  }

  60% {
    color: #fff;
    filter: blur();
    font-weight: 700;
  }

  90% {
    color: #e0287d;
    filter: blur(6px);
    font-weight: 500;
  }
}

.glitch span {
  animation: 5s step-end infinite paths;
}

.glitch:before {
  animation: 5s step-end infinite paths, 5s step-end infinite opacity, 8s step-end infinite font, 10s step-end infinite movement;
}

.glitch:after {
  animation: 5s step-end infinite paths, 5s step-end infinite opacity, 7s step-end infinite font, 8s step-end infinite movement;
}

@media (width >= 768px) {
  .button-49, .button-49:after {
    width: 200px;
    height: 86px;
  }
}

@keyframes glitch {
  0% {
    clip-path: var(--slice-1);
    transform: translate(-20px, -10px);
  }

  10% {
    clip-path: var(--slice-3);
    transform: translate(10px, 10px);
  }

  20% {
    clip-path: var(--slice-1);
    transform: translate(-10px, 10px);
  }

  30% {
    clip-path: var(--slice-3);
    transform: translate(0, 5px);
  }

  40% {
    clip-path: var(--slice-2);
    transform: translate(-5px);
  }

  50% {
    clip-path: var(--slice-3);
    transform: translate(5px);
  }

  60% {
    clip-path: var(--slice-4);
    transform: translate(5px, 10px);
  }

  70% {
    clip-path: var(--slice-2);
    transform: translate(-10px, 10px);
  }

  80% {
    clip-path: var(--slice-5);
    transform: translate(20px, -10px);
  }

  90% {
    clip-path: var(--slice-1);
    transform: translate(-10px);
  }

  100% {
    clip-path: var(--slice-1);
    transform: translate(0);
  }
}

@media (width >= 768px) {
  .button-49, .button-49:after {
    width: 200px;
    height: 86px;
  }
}

@media (prefers-color-scheme: dark) {
  html {
    --bg: #1e1e1e;
    --fg: #efefef;
    --gray: #aaa;
    --shadow: #2a2a2a;
    --light-gray: #444;
  }

  input:focus {
    box-shadow: inset 0 0 10em #ffffff05;
  }
}

main.please-wait .loader, main.please-wait .loader:after {
  display: inline-block;
}

.loader, .loader:after {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: none;
}

.loader {
  text-indent: -9999em;
  border-top: 3px solid var(--secondary);
  border-right: 3px solid var(--secondary);
  border-bottom: 3px solid var(--secondary);
  border-left: 3px solid #fff;
  font-size: 10px;
  animation: 1.1s linear infinite load8;
  position: absolute;
  top: calc(50% - 10px);
  left: calc(50% - 10px);
  transform: translateZ(0);
}

@keyframes load8 {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes spinny {
  0% {
    transform-origin: 50%;
    transform: translate3d(-50%, -50%, 0)rotateZ(0);
  }

  100% {
    transform: translate3d(-50%, -50%, 0)rotateZ(360deg);
  }
}

.spinny-loader {
  width: 100%;
  height: 100%;
  margin: 0 10px;
  display: inline-block;
  position: relative;
}

.spinny-loader > div {
  background-color: #3adb76 transparent8;
  border-radius: 200px;
  width: 60px;
  height: 60px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.spinny-loader:before, .spinny-loader:after {
  content: "";
  border: 15px solid #0000;
  border-color: #40ff00 #0000 #9500ff;
  border-radius: 200px;
  animation-name: spinny;
  animation-duration: .6s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
}

.spinny-loader:after {
  width: 140px;
  height: 140px;
  animation-direction: alternate-reverse;
}

.spinny-loader:before {
  width: 100px;
  height: 100px;
}

.spinny-loader {
  z-index: 99;
  opacity: 1;
  background-color: #1e1e1e99;
  position: fixed;
  inset: 0;
}

.spinny-loader.error > div {
  background-color: #ec5840;
}

.spinny-loader.error:before, .spinny-loader.error:after {
  border-color: #ec5840 #0000;
  animation-timing-function: linear;
}

.spinny-loader.error:before {
  animation-duration: 2.5s;
}

.spinny-loader.error:after {
  animation-duration: 3.8s;
  animation-direction: reverse;
}

.rotate {
  animation: 8s linear infinite rotation;
}

.logo-loader {
  z-index: 99;
  opacity: .95;
  background-color: #1e1e1ebf;
  position: fixed;
  inset: 0;
}

.logo-loader > img {
  width: 100%;
  height: 100%;
}

@keyframes rotation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(359deg);
  }
}

